.headerLogo {
  height: 3rem;
  margin: 0 1rem;
}
.app-navbar {
  top: 9rem;
}
.visibled {
  display: block;
}
.dropdown-menu {
  --bs-dropdown-min-width: 4rem;
}
.dropdown-item {
  text-transform: uppercase;
  color: #570723;
}

.show {
  display: block;
  position: absolute;
  right: 1rem;
  z-index: 1;
  background: #ffffff;
}
.dropdown-toggle {
  border: none;
  background: transparent;
}
.nav-item a {
  text-decoration: none;
  color: #092f63;
}
